@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

header {
    font-family: 'UD Shin Go Hangul Regular';
}

span {
    word-break: keep-all;
}

a {
    color: #8FC056;
    font-weight: bold;
    text-decoration: none;

    font-family: 'Raleway' 'UD Shin Go Hangul Medium', sans-serif;
    
    position: relative;
    padding: .2rem;
    padding-bottom: .5rem;

    text-shadow: 0px 0px 5px #292C32, 0px 0px 5px #292C32, 0px 0px 5px #292C32;
}

a:before {
    content: '';
		position: absolute;
		bottom: .1rem;
		left: 0; right: 0;
		height: .25rem;
		background-color: #8FC056;
    
		opacity: 0;
		transform: translateY(-1.5rem);
		transition: transform 0s cubic-bezier(0.550, 0.000, 0.265, 1.550), opacity 0s;
}

a:after {
    content: '';
    position: absolute;
    bottom: .1rem;
    left: 0; right: 0;
    height: .2rem;
    background-color: #8FC056;

    opacity: 0;
    transform: translateY(.2rem);
    transition: transform .3s cubic-bezier(0.550, 0.000, 0.265, 1.550), opacity .3s;
}

a:hover:before {
    opacity: 1;
    transform: translateY(-.3rem);

    transition: transform .3s cubic-bezier(0.550, 0.000, 0.265, 1.550), opacity .3s;
}

a:hover:after {
    opacity: 1;
    transform: translateY(-.3rem);

    transition: transform 0s .3s cubic-bezier(0.550, 0.000, 0.265, 1.550), opacity 0s .3s;
}
header, body {
    font-family: 'UD Shin Go Hangul Regular';
}
.stationMarker {
    width: 10px;
    height: 10px;
    border-style: solid;
    border-color: rgba(0, 0, 0, 1);
    border-width: 5px;
    border-radius: 10px;
}

.inputBox {
    font-family: 'UD Shin Go Hangul Regular';
    text-align: center;
    font-size: 2rem;
    color: #8fc056;
    
    background-color: rgba(0, 0, 0, 0);
    border-width: 0;
    border-right-width: 0;
    border-top-width: 0;
    border-left-width: 0;
}

.inputTextBlock {
    font-family: 'UD Shin Go Hangul Regular';
    font-size: 1.5rem;
    font-weight: 500;
}

input[type=submit] {
    font-family: 'UD Shin Go Hangul Regular';

    width: 140px;
    height: 55px;
    margin: 10px;

    background-color: #8fc056;

    font-weight: bold;
    color: #FFFFFF;

    border: none;
    border-radius: 50px;

    font-size: 1.3rem;
}
